import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

//Location Components
import { LocationsComponent } from './location/components/locations/locations.component'
import { MappedLocationComponent } from './location/components/mapped-location/mapped-location.component'

const routes: Routes = [
  {path: '', component: LocationsComponent},
  {path: 'location/map/:id', component: MappedLocationComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
